#cs-admin-tax-rates {
  margin: 1em 0 0;
  /**
	* [1] Due to the inability to reset the <tbody> child views the "empty" view
	* can only be appended to the parent. This means duplicates may be added.
	*
	* This can be removed once changes are immediately reflected with Backbone.sync()
	*/ }
  #cs-admin-tax-rates table {
    border-collapse: collapse; }
  #cs-admin-tax-rates .tablenav.top {
    display: flex;
    justify-content: space-between; }
  #cs-admin-tax-rates .cs-admin-tax-rates__tablenav--left {
    display: inline-flex; }
  #cs-admin-tax-rates th:not(.check-column) {
    padding: 15px 10px;
    width: unset; }
  #cs-admin-tax-rates .chosen-container {
    width: 100% !important; }
  #cs-admin-tax-rates tbody tr:not(:last-of-type) {
    border-bottom: 1px solid #e0e0e0; }
  #cs-admin-tax-rates tfoot.add-new th {
    font-weight: normal;
    padding: 12px 8px 10px 8px; }
  #cs-admin-tax-rates .cs-tax-rate-row--is-empty + .cs-tax-rate-row--is-empty,
  #cs-admin-tax-rates .cs-tax-rate-row--inactive {
    display: none; }
  #cs-admin-tax-rates .has-inactive .cs-tax-rate-row--inactive {
    display: table-row; }
  #cs-admin-tax-rates .cs-tax-rate-row--is-empty td {
    background-color: #f9f9f9; }
  #cs-admin-tax-rates .cs-tax-rate-row--inactive td {
    color: #999;
    background-color: #f9f9f9; }
  #cs-admin-tax-rates .cs-tax-rate-table-add {
    background-color: #f9f9f9; }
  @media screen and (max-width: 782px) {
    #cs-admin-tax-rates thead th:not(.cs-tax-rates-table-rate),
    #cs-admin-tax-rates tfoot:not(.add-new) th:not(.cs-tax-rates-table-rate) {
      display: none; }
    #cs-admin-tax-rates thead tr,
    #cs-admin-tax-rates tfoot:not(.add-new) tr,
    #cs-admin-tax-rates .cs-tax-rate-row {
      display: grid;
      grid-template-columns: 2.5em 1fr;
      grid-template-rows: 1fr;
      grid-gap: 0 16px; }
    #cs-admin-tax-rates th.cs-tax-rates-table-rate {
      padding-left: 12px; }
    #cs-admin-tax-rates .cs-tax-rates-table-checkbox {
      grid-row: 1 / 5; }
    #cs-admin-tax-rates tbody td {
      padding-left: 35% !important; }
    #cs-admin-tax-rates td:before {
      content: attr(data-colname);
      display: block;
      width: 32%;
      position: absolute; }
    #cs-admin-tax-rates .tablenav.top {
      flex-wrap: wrap; }
    #cs-admin-tax-rates .cs-admin-tax-rates__tablenav--left {
      margin-bottom: 16px; }
    #cs-admin-tax-rates .cs-admin-tax-rates__tablenav--left select {
      margin-right: 6px; } }

.cs-tax-rate-table-add th select,
.cs-tax-rate-table-add th input[type="text"],
.cs-tax-rate-table-add th input[type="number"] {
  width: 100%;
  margin: 0;
  padding: 4px; }

@media screen and (max-width: 782px) {
  .cs-tax-rate-table-add {
    display: block; }
    .cs-tax-rate-table-add th {
      display: block; }
    .cs-tax-rate-table-add .screen-reader-text {
      display: block;
      width: unset;
      clip: unset;
      height: unset;
      clip-path: unset;
      margin: 0 0 12px;
      position: relative; } }
